.social {
  margin: 0;
  padding-top: 20px;
  padding-bottom: 21px;

  background-color: $color-main-blocks;
  border-bottom: 1px solid $color-index-borders;

  @media (min-width: $tablet-width) {
    border-bottom: none;
  }
}

.social__list {
  display: flex;
  justify-content: center;
  align-items: flex-end;

  @include list-reset();
}

.social__item {
  margin-right: 33px;

  &:last-child {
    margin-right: 0;
  }
}

.social__link {
  &:focus .social__img {
    @include my-focus();
  }

  &:active .social__img {
    @include my-active();

    opacity: 0.3;
  }
}
